@keyframes path-anim 
  to
    stroke-dashoffset: 0

@keyframes path-page-anim 
  from 
    transform: translateY(100%)
  to 
    stroke-dashoffset: 0
    transform: translateY(0)

.nothing  
  & path:nth-child(1)
    @include svg-about-animation
    stroke-dasharray: 449px
    stroke-dashoffset: 449px

  & path:nth-child(2)
    @include svg-about-animation
    stroke-dasharray: 82px
    stroke-dashoffset: 82px

  & path:nth-child(3) 
    @include svg-about-animation
    stroke-dasharray: 145px
    stroke-dashoffset: 145px

  & path:nth-child(4) 
    @include svg-about-animation
    stroke-dasharray: 33px
    stroke-dashoffset: 33px

  & path:nth-child(5) 
    @include svg-about-animation
    stroke-dasharray: 13px
    stroke-dashoffset: 13px

  & path:nth-child(6) 
    @include svg-about-animation
    stroke-dasharray: 201px
    stroke-dashoffset: 201px

  & path:nth-child(7) 
    @include svg-about-animation
    stroke-dasharray: 55px
    stroke-dashoffset: 55px

  & path:nth-child(8) 
    @include svg-about-animation
    stroke-dasharray: 104px
    stroke-dashoffset: 104px

  & path:nth-child(9)
    @include svg-page-animation
    stroke-dasharray: 243px
    stroke-dashoffset: 243px

