    stroke-dashoffset: 221px

  & path:nth-child(5) 
    @include svg-about-animation
    stroke-dasharray: 150px
    stroke-dashoffset: 150px

  & path:nth-child(6) 
    @include svg-page-animation
    stroke-dasharray: 290px
    stroke-dashoffset: 290px

  & path:nth-child(7) 
    @include svg-page-animation
    stroke-dasharray: 41px
